<template>
  <div id="app">
    <div class="content">
      <m-header></m-header>
      <m-tab></m-tab>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
      <player></player>
    </div>
    <div class="startImg" v-if="isStartImg">
    </div>
  </div>
</template>

<script>
import MHeader from './components/MHeader/m-header'
import Tab from './components/tab/tab'
import Player from './components/player/player'
export default {
  name: 'App',
  components: {
    'm-header': MHeader,
    'm-tab': Tab,
    Player
  },
  data(){
    return {
      isStartImg: true
    }
  },
  mounted(){
    setTimeout(()=>{
      this.isStartImg = false
    }, 2000);
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "common/stylus/variable"
#app
  color: $color-theme
  .startImg
    position: fixed
    top: 0
    width: 100%
    bottom: 0
    background: url('./common/image/start.png') no-repeat
    background-size: cover
</style>



