import jsonp from 'common/js/jsonp'
import {commonParams, options, baseUrl} from './config'
import axios from 'axios'

const debug = process.env.NODE_ENV !== 'production'
export function getSearchList(searchStr, page) {
	//const url = debug ? '/api/search' : 'http://yujian.store/music/api/search'
	// const url = '/api/search'
	const url =  baseUrl + "/api/music/search"
	const data = Object.assign({}, commonParams, {
		w: searchStr,
		p: page,
		perpage: 20,
		n: 20,
		zhidaqu: 1,
		catZhida: 1,
	    t: 0,
	    flag: 1,
	    ie: 'utf-8',
	    sem: 1,
	    aggr: 0,
	    remoteplace: 'txt.mqq.all',
	    uin: 0,
	    needNewCode: 1,
	    platform: 'h5',
	    format: 'json'
  })

	return axios.get(url, {
		params: data
	}).then((res) => {
		return Promise.resolve(res, data)
	})
}

export function getHotKey() {
	const url = 'https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg'
	const data = Object.assign({}, commonParams, {
    g_tk: 5381,
		jsonpCallback: 'hotSearchKeysmod_search',
		loginUin: 0,
		hostUin: 0,
		notice: 0,
		platform: 'yqq',
		needNewCode: 0,
  })
  return jsonp(url, data, options)
}


